@use '../../var';

nav {
    position: fixed;
    z-index: 1;
    width: 15vw;
    height: 100%;
    flex-direction: column;
    text-align: right;
    padding: 1rem;
    font-size: 1.5rem;
    box-shadow: 0 3px 5px 0 var.$secondary-color;
    background-color: var.$primary-color;
    color: var.$secondary-color;
}

nav h1 {
    margin-top: 1rem;
    cursor: pointer;
    text-align: center;
    background-color: var(--secondary-color);
}

nav ul {
    margin-top: 18rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 1.9rem;
    background-color: var(--secondary-color);
}

nav .icon {
    color: var.$secondary-color;
    width: 2rem;
}

nav li {
    font-size: 2rem;
    cursor: pointer;
    font-weight: bold;
}

nav a {
    background-color: var(--secondary-color);
    color: var(--main-color);
    text-decoration: none;
    background-image: linear-gradient(var.$secondary-color, var.$secondary-color);
    background-size: 0% 0.1em;
    background-position-y: 100%;
    background-position-x: 0%;
    background-repeat: no-repeat;
    transition: background-size 0.2s ease-in-out;
}

nav a:hover,
nav a:focus,
nav a:active {
    font-weight: bolder;
    background-size: 100% 0.1em;
}

.burgerMenu {
    display: none;
    font-size: 2rem;
    border: 0;
    background-color: transparent;
    cursor: pointer;
}

.burgerMenu img {
    border-radius: 5px;
}

.show {
    transform: translateX(0);
    transition: transform 0.5s ease-in;
}


@media only screen and (max-width: 1000px) {
    
    nav {
        display: flex;
        flex-direction: row;
        width: 100%;
        height: auto;
        position: fixed;
        z-index: 1;
        justify-content: space-between;
        font-size: 1.5rem;
        box-shadow: 0 3px 5px 0 var.$primary-color;
        background-color: var.$secondary-color;
        color: var.$primary-color;
    }

    nav h1 {
        margin-top: .6rem;
    }

    nav ul {
        margin: 5rem 0 0 0;
        position: fixed;
        flex-direction: column;
        top: 0;
        right: 0;
        padding: 1rem;
        width: 200px;
        height: 100%;
        text-align: center;
        background-color: var.$secondary-color;
        color: var.$primary-color;
        transform: translateX(120%);
    }

    .burgerMenu {
        display: block;
    }

    .toggle {
        display: none;
    }

}