@use '../../var';

.frontpage{
    z-index: 2;
    position: absolute;
    width: 100%;
    height: 100vh;
    // animation: fadeIn 1s ease-in both;
    background-color: var.$primary-color;
    color: var.$secondary-color;
}

.welcome {
    z-index: 1000;
    max-width: 1920px;
    min-width: 300px;
    width: 60%;
    height: 100vh;
    margin: 0 auto;
    position: relative;
    text-align: center;
}


.welcome div {
    margin: 0;
    position: absolute;
    width: 50%;
    top: 40%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
}

.welcome ul {
    display: flex;
    width: 300px;
    margin: 0 auto;
}

.welcome ul li {
    width: 33%;
    height: auto;
    margin: 0 5px;
    text-align: center;
    line-height: 100px;
    font-size: 2rem;
    font-weight: bold;
    background-image: linear-gradient(to bottom right, var.$secondary-color, var.$primary-color);
    // background-color: var.$secondary-color;
    color: var.$secondary-color;
    animation: fadeIn .5s ease-in both;
}

.welcome ul li:nth-child(2) {
    animation-delay: .5s;
}

.welcome ul li:nth-child(3) {
    animation-delay: 1s;
}

.welcome .title {
    font-size: 2rem;
    margin: 1rem;
    animation: fadeIn 1s ease-in both;
    animation-delay: 1.5s;
}

.welcome button {
    font-size: 1.5rem;
    border: none;
    width: 250px;
    height: 50px;
    background-color: var.$primary-color;
    color: var.$secondary-color;
    font-weight: bold;
    animation: fadeIn 1s ease-in both;
    animation-delay: 1.5s;
    background-size: 0% 0.1em;
    background-position-y: 100%;
    background-position-x: 0%;
    background-repeat: no-repeat;
    transition: background-size 0.2s ease-in-out;
}

.welcome button:hover {
    background-image: linear-gradient(to bottom right, var.$primary-color, var.$secondary-color);
    color: var.$secondary-color;
    transform: scale(1.1);
    transition: all .5s ease-in-out;
    background-size: 100% 0.1em;
}

// @keyframes fadeIn {
//     0% {opacity:0;}
//     100% {opacity:1;}
// }

@keyframes fadeIn {
	from {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	to {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
}



@media only screen and (max-width: 1200px) {

    .welcome div {
        width: 100%;
    }
}